import * as React from 'react';
import { connect } from 'react-redux';
import SignUp from './SignUp';
import NumberMemory from './NumberMemory';
import ReactionTime from './ReactionTime';
import VerbalMemory from './VerbalMemory';
import VisualMemory from './VisualMemory';
import FraudDetection from './FraudDetection';
import LockedScreen from './LockedScreen';
import './Main.css'
import { GameReducerType_NumberMemory, GameReducerType_ReactionTime, GameReducerType_VerbalMemory, GameReducerType_VisualMemory, GameReducerType_FraudDetection, GameReducerType_Locked } from '../constants';
import { useHotkeys } from 'react-hotkeys-hook';
import LeadershipBoard from './LeadershipBoard';

const Main = ({ user, game, setGame }) => {
    const [showLeaderBoard, setShowLeaderBoard] = React.useState(false);

    useHotkeys('control+shift+1', () => {
        setGame(GameReducerType_NumberMemory);
    });

    useHotkeys('control+shift+2', () => {
        setGame(GameReducerType_ReactionTime);
    });

    useHotkeys('control+shift+3', () => {
        setGame(GameReducerType_VerbalMemory);
    });

    useHotkeys('control+shift+4', () => {
        setGame(GameReducerType_VisualMemory);
    });

    useHotkeys('control+shift+5', () => {
        setGame(GameReducerType_FraudDetection);
    });

    useHotkeys('control+shift+l', () => {
        setGame(GameReducerType_Locked);
    });

    useHotkeys('alt+shift+7', () => {
        setGame(GameReducerType_NumberMemory);
    });

    useHotkeys('control+shift+9', () => {
        setShowLeaderBoard(true);
    });

    useHotkeys('control+c', () => {
        return false;
    })

    useHotkeys('control+v', () => {
        return false;
    })

    useHotkeys('f5,control+r,command+r', (ke) => {
        ke.preventDefault();
        console.log("Refresh handled...")
    });

    React.useEffect(() => {
        let intervalHandle = null;
        if (showLeaderBoard) {
            let id = 0;
            const gameTypes = [GameReducerType_NumberMemory, GameReducerType_VerbalMemory, GameReducerType_VisualMemory, GameReducerType_FraudDetection];
            intervalHandle = setInterval(() => {
                if (id === 4) {
                    id = 0;
                }

                setGame(gameTypes[id++]);

                return () => {
                    if (intervalHandle) {
                        clearInterval(intervalHandle);
                        intervalHandle = null;
                    }
                }
            }, 10000);
        } else {
            if (intervalHandle) {
                clearInterval(intervalHandle);
                intervalHandle = null;
            }
        }
    }, [showLeaderBoard]);

    return (
        <div className="App flex flex1 height-100">
            <div className="flex flex1 justify-center">
                <div className="width-90vw flex">
                    <div className="Main-content flex flex1 mar-top-30">
                        {showLeaderBoard ? <div className="flex flex1 justify-center bg-orange visual-test"><LeadershipBoard game={game} /></div> : game === GameReducerType_Locked ? <LockedScreen /> : !user ? <SignUp /> : <Game game={game} />}
                    </div>
                </div>
            </div>
        </div>
    );

};

const Game = ({ game }) => {
    switch (game) {
        case GameReducerType_NumberMemory:
            return <NumberMemory />;
        case GameReducerType_ReactionTime:
            return <ReactionTime />;
        case GameReducerType_VerbalMemory:
            return <VerbalMemory />;
        case GameReducerType_VisualMemory:
            return <VisualMemory />;
        case GameReducerType_FraudDetection:
            return <FraudDetection />
        default:
            return null;
    }
};

const mapStateToProps = ({ user, game }) => ({
    user, game
});

const mapDispatchToProps = (dispatch) => ({
    setGame: (gameType) => dispatch({ type: gameType })
});

export default connect(mapStateToProps, mapDispatchToProps)(Main);